import React, { Component } from "react";
import { connect } from "react-redux";
import { getUserInfo, reSendEmail } from "../../../actions/loginActions";
import { TOAST_DISMISS, TOAST_SUCCESS } from "../../../utils";
import withRouter from "../../routes/WithRouter";
import { onEvent } from "../../../actions/socketClient";

class accountVerify extends Component {
    componentWillMount() {
        const { navigate, authorized, getUserInfo } = this.props;
        this.reSendEmailCall()
        onEvent(`update${localStorage.getItem("ownerId")}`, (data) => {
            getUserInfo(navigate)
        });
    }

    reSendEmailCall=()=>{
        var email= localStorage.getItem("emailId") 
        TOAST_SUCCESS('Verification link successfully sent');
        reSendEmail(email);
        setTimeout(() => { TOAST_DISMISS()}, 2000);
    }
    onSignOut=()=>{
        localStorage.clear()
        this.props.navigate('/accounts/register')
    }



    render() {
        return (
            <div role='dialog' aria-modal="true" className='fade modal show' tabIndex='-1' style={{ display: 'block', paddingLeft: '6px'}}>
                <div className='modal-dialog modal-lg modal-dialog-centered'>
                    <div className='modal-content'>
                        <div id='email_confirmation' className='model fade show'>
                            <div className='modal-body'>
                                {/* <button type="button" className="btn" data-bs-dismiss="modal" onClick={this.onSignOut}>
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 32 32"><g id="Group_5147" data-name="Group 5147" transform="translate(-282 -85)"><rect id="Rectangle_2245" data-name="Rectangle 2245" width="28" height="28" transform="translate(282 85)" fill="none"></rect><g id="_15" data-name="15" transform="translate(292.278 91)"><path id="Path_5609" data-name="Path 5609" d="M18.238,25.487a1.463,1.463,0,0,0,1.039-2.5l-7.755-7.74L19.277,7.5A1.469,1.469,0,0,0,17.2,5.426L8.42,14.206a1.463,1.463,0,0,0,0,2.063L17.2,25.048a1.463,1.463,0,0,0,1.039.439Z" transform="translate(-7.994 -4.996)" fill="#545454"></path></g></g></svg>
                                </button> */}
                                <div className='btn p-0 text-muted'>
                                    <div className='mb-10'>
                                        <div className='text-center pb-lg-3'>
                                            <img className='img-fluid mb-10' src='https://cloud.taggbox.com/taggshop/app/assets/media/dashboard/email_icon.svg' height={185} width={276} alt='email-icon' />
                                            <h3 className='text-center mb-5'>You're just a step away.</h3>
                                            <p className='font-weight-bold text-dark-75 text-center'>
                                                We have sent an email to <strong className='font-weight-boldest'>{localStorage.getItem("emailId")} </strong>  
                                                    with the verification link. Please go to your email account to verify. If you don't see the email in your inbox or spam folder, try resending the email here:
                                            </p>
                                        </div>
                                        <div className='d-flex align-items-center justify-content-center pt-lg-3'>
                                            <div className='d-flex align-items-start justify-content-center w-100 m-auto' style={{maxWidth: '420px'}}>
                                                <div className='form-group text-start mb-3 w-100'>
                                                    <input className='form-control form-control-lg border-secondary' type='test' placeholder='example@mail.com' name='emailId' defaultValue={localStorage.getItem("emailId")} readOnly />
                                                    <span id="accountsemailIderror" className="error d-block m-0"></span>
                                                </div>
                                                <button type="button" className='btn btn-md btn-primary ml-2 text-nowrap mb-3' onClick={()=>this.reSendEmailCall()}>Resend Email</button>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
    }
}
const mapDispatchToProps = dispatch => {
    return { 
    //  reSendEmail: (email) => dispatch(reSendEmail(email))
    getUserInfo: (navigate) => dispatch(getUserInfo(navigate))
      
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(accountVerify));